import React from 'react'
import { useNavigate } from 'react-router-dom'

const Service = ({service, img}) => {

    const navigate = useNavigate()

    const goto = () => {
        if(service !== 'Scan Document') return 
        navigate('/scan')
    }

  return (
    <div className='service' onClick={ () => goto(service)}>
        <img src={img}/>
        <h4 className='bgBtn'>{service}</h4>

    </div>
  )
}

export default Service