import React, { useContext, useState, useEffect } from 'react'
import Service from './Service'
import { ScanContext } from '../App'
import Title from './common/Title'

const Services = () => {
    const services = [ "Check Balance", "Scan Document", "Check Reports"]

    const listDetails = useContext(ScanContext)

  return (
    <div className='contentCenter'>
        {/* <Title title={`Details for policy number : ${listDetails.newScanlist.policy}`}/> */}
        <h3>Details for policy number : <span>{listDetails.newScanlist.policy}</span></h3>
        <div className='serviceList'>
            {
                services.map( (service, index) => <Service key={service} service={service} img={`${process.env.PUBLIC_URL}/img/s${ index + 1 }.png`} />)
            }
        </div>
    </div>
  )
}

export default Services