import React, { useContext, useState, useEffect } from 'react';
import { Pie } from 'react-chartjs-2';
import { Chart as ChartJS } from 'chart.js/auto'

import { ScanContext } from '../App'



const PieChart = () => {

  const [ newState, setNewState ] = useState()  
  const [ chartColors, setChartColors ] = useState([])
  const [ barData, setbarData ] = useState([])
  const listDetails = useContext(ScanContext)

  useEffect(() => {
    setChartColors(listDetails.newScanlist.colors)
    setbarData([listDetails.newScanlist.documentScanned.length, listDetails.newScanlist.documentAccepted.length, listDetails.newScanlist.documentRejected.length])
  },[])


  const state = {
    labels: [`Scanned ${listDetails.newScanlist.documentScanned.length}`, `Approved ${listDetails.newScanlist.documentAccepted.length}`, `Rejected ${listDetails.newScanlist.documentRejected.length}`],
    datasets: [
      {
        label: 'Scanned Results',
        backgroundColor: chartColors.map( color => `rgba(${color},1)`),
        borderColor: 'rgba(0,0,0,1)',    
        borderWidth: 0,
        data: barData
      }
    ]
  }


  return (
    <Pie
          data={state}
          // width={100}
          // height={50}
          options={{
            title:{
              display:true,
              text:'Documnet Scanning stats',
              fontSize:20
            },
            legend:{
              display:true,
              position:'right'
            },
            // layout: {
            //   padding: 20
            // },
            // maintainAspectRatio: false
          }}
        />
  )
}

export default PieChart