import React, { useContext, useState, useEffect } from 'react'
import { ScanContext } from '../../App'
import { useNavigate } from 'react-router-dom'

const Modal = () => {

    const navigate = useNavigate()
    const listDetails = useContext(ScanContext)

    console.log(listDetails.newScanlist.modal.success)

    const closeModal = () => {
        console.log('Modal close')
        listDetails.dispatch({ type:'modal', value: { stat: false, msg: '', success: false }})
    }


    const goto = () => {
        listDetails.newScanlist.modal.success ? navigate('/dashboard') : navigate('/rescan')
        listDetails.dispatch({ type:'modal', value: { stat: false, msg: '', success: false }})
    }

  return (
    <div className={`modal `}>
        
        <div className={`modalPanel ${ listDetails.newScanlist.modal.success ? 'bgSuccess' : 'bgGray' }`}>
            {/* <button className='bgBtn roundBtn' onClick={closeModal}>X</button> */}
            
            <h3>{listDetails.newScanlist.modal.msg}</h3>
            
            <button className='bgBtn' onClick={goto}>
                { listDetails.newScanlist.modal.success ? 'Save' : 'Rescan' }
            </button>
        </div>
        
    </div>
  )
}

export default Modal