import React, { useContext, useState, useEffect } from 'react'
import { ScanContext } from '../App'
import Title from './common/Title'
import Loading from './common/Loading'

const ScanSuccess = () => {

  const listDetails = useContext(ScanContext)

  const [ scanned, setScanned ] = useState(false)
  const [ successMsg, setSuccessMsg ] = useState('')
 
  useEffect(() => {

    if(listDetails.newScanlist.scannedItem.name === '') return

    let scanned = Math.floor(Math.random() * 2)

    setTimeout(() => {
      setScanned(true)
      
      let msg = ''
      if(scanned === 1){
        msg ='Scanning is successful.'
      }else{
        msg ='Scanning failed, please rescan.'
      }

      listDetails.dispatch({type:'scannedItem', value: {name:listDetails.newScanlist.scannedItem.name, status: scanned === 1 ? true: false }})
      listDetails.dispatch({type:'modal',value: { stat: true, msg, success: scanned === 1 ? true: false} })
    
    }, 5000)

  },[])

  return (
    <div className='contentCenter'>
      {/* <Title title={`Scanning ${listDetails.newScanlist.scannedItem.name} for policy number : ${listDetails.newScanlist.policy}`}/> */}
      <h3>Scanning : <span>{listDetails.newScanlist.scannedItem.name}</span> for policy number : <span>{listDetails.newScanlist.policy}</span></h3>
      {
        scanned ? <p>{ successMsg }</p> : <Loading />
      }
      
    </div>
  )
}

export default ScanSuccess