
import React, {useState, useReducer, useEffect} from 'react';
import { HashRouter as Router, Routes, Route} from 'react-router-dom'
import './App.css';
import Login from './view/Login';
import Dashboard from './view/Dashboard';
import Services from './view/Services';
import DocAuth from './view/DocAuth';
import ScanList from './view/ScanList';
import ScanSuccess from './view/ScanSuccess'
import PrivateRoutes from './view/PrivateRoutes';
import Modal from './view/common/Modal';

export const ScanContext = React.createContext()

function App() {

  const scanState = {
    loggedIn : false,
    user : '',
    policy:'',
    scannedItem : { name : '', status : false},
    modal : { stat: false, msg: '', success: false},
    documentScanned: ["Policy Document", "Bank Statement", "Adhar Card", "Pan Card", "Address Proof"],
    documentAccepted: ["Policy Document", "Bank Statement", "Adhar Card"],
    documentRejected: ["Pan Card", "Address Proof"],
    colors : ["0, 26,119", " 66, 173, 0", "219, 79, 0", "0, 71, 255", "255, 163, 0", "67, 67, 67"]
    

  }

  const [ scanList = scanState, setScanList ] = useState()

  const reducer = (state = scanState, action) => {
    switch(action.type){
      case 'loggedIn':
                return { ...state, "loggedIn" : action.value}    
      case 'user':
                return { ...state, "user" : action.value}          
      case 'policy':
                return { ...state, "policy" : action.value}
      case 'scannedItem':
                return { ...state, "scannedItem" : action.value}     
      case 'modal':
                return { ...state, "modal" : action.value}               
      case 'default':
                return [...state]
    }
  }

  const [ newScanlist, dispatch] = useReducer(reducer, scanList)

  return (
    <div className="displayScreen bgMain">
      <ScanContext.Provider value={{newScanlist, dispatch}}>
      <Router>
          
            
            <Routes>
              <Route path="/" element={<Login />} />
              <Route path="/dashboard" element={<PrivateRoutes  loggedIn={ newScanlist.loggedIn }/>} >
                <Route path="/dashboard" element={<Dashboard />} />
              </Route>
                <Route path="/services" element={<PrivateRoutes loggedIn={ newScanlist.loggedIn }/>} >
                <Route path="/services" element={<Services />} />
              </Route>  
             
              <Route path="/docauth" element={<PrivateRoutes loggedIn={ newScanlist.loggedIn }/>} >
                <Route path="/docauth" element={<DocAuth />} />
              </Route>
              <Route path="/scan" element={<PrivateRoutes loggedIn={ newScanlist.loggedIn }/>} >
                <Route path="/scan" element={<ScanList />} />
              </Route>
              <Route path="/success" element={<PrivateRoutes loggedIn={ newScanlist.loggedIn }/>} >
                <Route path="/success" element={<ScanSuccess />} />
              </Route>
              <Route path="/rescan" element={<PrivateRoutes loggedIn={ newScanlist.loggedIn }/>} >
                <Route path="/rescan" element={<ScanList />} />
              </Route>
            </Routes>
            { newScanlist.modal.stat && <Modal /> }
      
      </Router>
      
      </ScanContext.Provider>
      <h5 style={{position:'absolute', bottom: '-10px', left: '10px'}}>This React app is created with happy path. No data is stored in backend. You can use any login details and policy number as input.</h5>
    </div>
  );
}

export default App;
