import React, { useContext, useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { ScanContext } from '../App'

const ScanItem = ({item}) => {

  const navigate = useNavigate()
  const listDetails = useContext(ScanContext)

  const scannIt = (data ) => {
    listDetails.dispatch({type:'scannedItem', value: {name:data, status:false}})
    navigate('/success')
  }
  return (
    <form className='scanForm'>
        {/* <input type='radio' /> */}
        <div className='docName'>{item}</div>
        <button className='bgBtn' onClick={() => scannIt(item)}>Scan</button>
    </form>
  )
}

export default ScanItem