import React from 'react'
import ScanItem from './ScanItem'
import Title from './common/Title'

const ScanList = () => {
  const scanList = [
    "Policy Document",
    "Bank Statement",
    "Adhar Card",
    "Pan Card",
    "Address Proof",
    "Premium Receipt"
  ]
  return (
    <div className='contentCenter'>
      {/* <Title title="List of documents to be scanned" /> */}
      <h3>List of documents to be scanned</h3>
      <div className='scanList'>
        {
          scanList.map( (item, index ) => <ScanItem key={item} item={item} />)
        }
      </div>
    </div>
  )
}

export default ScanList