import React, { useContext, useState, useEffect } from 'react'
import { useForm } from 'react-hook-form'
import { DevTool } from '@hookform/devtools'
import { useNavigate } from 'react-router-dom'
import { ScanContext } from '../App'

const DocAuth = () => {

  
  const navigate = useNavigate()

  const listDetails = useContext(ScanContext)

  const form = useForm()
  const { register, control, handleSubmit } = form

  const onSubmit = (data) => {
      // console.log("Data ", data)
      // console.log(listDetails)
      if(data.policy !== '' ){
          listDetails.dispatch({type:'policy', value: data.policy})
          navigate('/dashboard')
      }
  }

  const goto = (  ) => {
    
  }

  return (
    <div className='contentCenter'>
      <p>Provide policy number</p>
      <form className='scanForm' onSubmit={handleSubmit(onSubmit)} noValidate >
        <input type='text' {...register('policy', {required:'Policy Number'})}/>
        
        <button className='bgBtn' onClick={() => goto()}>Submit</button>
      </form>
      <DevTool control={control}/>
    </div>
  )
}

export default DocAuth