import React, { useContext, useState, useEffect } from 'react'
import { ScanContext } from '../App'

const PolicyDetails = () => {
    const listDetails = useContext(ScanContext)

  return (
    <div className="policyDetails">
        <div className='policyRow'>
            <p className='policyRowTitle'>Policy No.</p>
            <p>{listDetails.newScanlist?.policy}</p>
        </div>
        <div className='policyRow'>
            <p className='policyRowTitle'>Documents Scanned</p>
            {listDetails.newScanlist?.documentScanned.map( name =>
                <p key = {name} >{name} 
                    {
                        (listDetails.newScanlist?.documentAccepted.includes(name)) ? <span style={{background:'rgba(66, 173, 0, 1)'}}>Accepted</span> : <span style={{background:'rgba(219, 79, 0, 1)'}}>Rejected</span>
                    }
                </p>
            )}
        </div>
        
    </div>
  )
}

export default PolicyDetails