import React, { useContext, useState, useEffect } from 'react'
import { useForm } from 'react-hook-form'
import { DevTool } from '@hookform/devtools'
import { useNavigate } from 'react-router-dom'
import { ScanContext } from '../App'


const Login = () => {

    const navigate = useNavigate()
    const listDetails = useContext(ScanContext)

    const form = useForm()
    const { register, control, handleSubmit } = form

    const onSubmit = (data) => {
        // console.log("Data ", data)
        // console.log(listDetails)
        if(data.email !== '' && data.password !== ''){
            listDetails.dispatch({type:'loggedIn', value: true})
            listDetails.dispatch({type:'user', value: data.email})
        }
    }

    useEffect(()=> {
        if(!listDetails.newScanlist.loggedIn) return
        // console.log(listDetails)

        navigate('/docauth')

    },[listDetails])

  return (
    <div className='contentCenter'>
        <div className='login'>

            <h3>Welcome to Scan-Doc</h3>
            <form onSubmit={handleSubmit(onSubmit)} noValidate>
                <input 
                    type='email' 
                    id='email' 
                    placeholder='abc@xyz.com'  
                    {...register('email', {required:'Email Required'})}
                />
                <input 
                    type='password' 
                    id='password' 
                    placeholder='password'  
                    {...register('password', {required:'Password Required'})}
                />
                <button className='bgBtn'>Submit</button >
            </form>
            <DevTool control={control}/>
        </div>
    </div>
  )
}

export default Login